


// 数据请求
$.ajax({
    url:'/getAirportOnLineData',
    type:'get',
    dataType:'jsonp',
    success:(data)=>{
        sessionStorage.setItem('linesData',JSON.stringify(data));
    }
})
;$.ajax({
    url:'/restful/getCityDatas',
    type:'get',
    dataType:'jsonp',
    success:(data)=>{
        sessionStorage.setItem('cityData',JSON.stringify(data.success.cityData));
    }
}) ;
$.ajax({
    url:'/restful/getShippingDatas',
    type:'get',
    dataType:'jsonp',
    success:(data)=>{
        sessionStorage.setItem('department',JSON.stringify(data.success.cityData));
    }
});
$.ajax({
    url:'/restful/getAirportJosnDatas',
    type:'get',
    dataType:'jsonp',
    success:(data)=>{
        sessionStorage.setItem('airportData',JSON.stringify(data.success.cityData));
    }
});
$.ajax({
    url:'/getAirportViewData',
    type:'get',
    dataType:'jsonp',
    success:(data)=>{
        sessionStorage.setItem('airjw',JSON.stringify(data));
    }
});
